<template>
	<div class="box">
		<div class="record">
			<not-record v-if="!list.length"></not-record>
			<div class="header">{{ $t('message.changeRecord') }}</div>
			<div class="content">
				<div v-for="(item, index) of list" :key="index">
					<div>
						<span></span>
						<span>+{{ item.sfc }} SFC</span>
					</div>
					<div>
						<span>{{ item.time }}</span>
						<span>-{{ item.eth }} ETH</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import NotRecord from '@/components/not-record.vue'
	import { isWallet  } from '../../static/initData'
	
	export default {
		components: { NotRecord },
		data() {
			return {
				list: []
			}
		},
		methods: {
			init() {
				this.$contract.tokenChanger.events.Log_ChangedToken({
					filter: { owner: this.$address },
					fromBlock: 0
				}).on('data', res => {
					let time = new Date(Number(res.returnValues.time) * 1000),
					  year = time.getFullYear(),
				  	  month = time.getMonth() + 1,
				  	  day = time.getDate()
				
					this.list.push({
						sfc: this.$fromWei(res.returnValues.tokenValue),
						eth: this.$fromWei(res.returnValues.tokenValue) / 3000,
						time: `${year}.${month}.${day}`
					})
					console.log(res)
				}).on('error', console.log)
			}
		},
		mounted() {
			if (isWallet) {
			    this.init()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		width: 100%;
		padding-top: 14px;
		height: calc(100% - 100px);
		&>div {
			width: 100%;
			margin-bottom: 6px;
			padding: 0 50px;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				color: #A1BDE1;
				font-size: 20px;
				line-height: 30px;
				&:first-child>span:first-child {
					color: #28AC79;
				}
			}
		}
	}
	.header {
		width: 600px;
		height: 100px;
		color: #fff;
		font-size: 32px;
		font-weight: bold;
		margin: 0 auto;
		line-height: 100px;
		border-bottom: 2px solid #0D0B3A;
	}
	.record {
		position: relative;
		width: 100%;
		height: 100%;
		border-radius: 10px;
		background-color: #25234D;
	}
	.box {
		width: 100vw;
		height: 100vh;
		padding: 25px 20px 32px;
	}
</style>
