<template>
	<div class="box">
		<div class="header">
			<span :class="show ? 'active' : ''" @click="show = true">{{ $t('message.joinRecord') }}</span>
			<span :class="show ? '' : 'active'" @click="show = false">{{ $t('message.incomeRecord') }}</span>
		</div>
		<record
		  v-show="show"
		  :title="[$t('message.joinAmount'), $t('message.joinTime')]"
		  :list="joinList"
		  span="12"
		></record>
		<record
		  v-show="!show"
		  :title="[$t('message.type'), $t('message.number'), $t('message.time')]"
		  :list="incomeList"
		  span="8"
		></record>
	</div>
</template>

<script>
	import Record from '@/components/home-record.vue'
	
	export default {
		components: { Record },
		data() {
			return {
				show: true,
				joinList: [],
				incomeList: []
			}
		},
		methods: {
			init() {
				if (this.$cache.record) {
					this.joinList = this.$cache.record.joinList
					this.incomeList = this.$cache.record.incomeList
				} else {
					this.$cache.record = {}
				}
				
				this.getData()
			},
			getData() {
				let flag1 = true, flag2 = true
				
				this.$contract.roundWS.events.Log_NewDeposited({
				    filter: { owner: this.$address },
				    fromBlock: 0
				}).on('data', res => {
					if (this.joinList.length > 0 && flag1) {
						this.joinList = []
						flag1 = false
					}
					
					let time = new Date(Number(res.returnValues.time) * 1000),
						year = time.getFullYear(),
						month = time.getMonth() + 1,
						day = time.getDate()
						
				    this.joinList.unshift({
						amount: `${this.$fromWei(res.returnValues.value)}ETH`,
						time: `${year}/${month}/${day}`
					})
					this.$cache.record.joinList = this.joinList
				}).on('error', console.error)
				
				this.$contract.roundWS.events.Log_ProfixHistory({
				    filter: { owner: this.$address },
				    fromBlock: 0
				}).on('data', res => {
					if (this.incomeList.length > 0 && flag2) {
						this.incomeList = []
						flag2 = false
					}
					
				    let type = Number(res.returnValues.ptype),
				        time = new Date(Number(res.returnValues.time) * 1000),
				        year = time.getFullYear(),
				        month = time.getMonth() + 1,
				        day = time.getDate()
				        
				    if (type >= 0 && type < 15) {
				        type = this.$t('message.sharingAward')
				    } else if (type > 20 && type < 25) {
				        type = `V${res.returnValues.ptype.substr(1, 1)}`
				    } else if (type > 30 && type < 35) {
				        type = this.$t('message.tieAward')
				    } else if (type === 40) {
				        type = this.$t('message.dividend')
				    }
				    
				    this.incomeList.unshift({
				        type: type,
				        amount: `+${this.$fromWei(res.returnValues.value)} ETH`,
				        time: `${year}/${month}/${day}`
				    })
					this.$cache.record.incomeList = this.incomeList
				}).on('error', console.error)
			}
		},
		mounted() {
			if (this.$route.query.type === '') {
				this.show = false
			}
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.header {
		width: 500px;
		height: 80px;
		color: #E8E9EA;
		font-size: 32px;
		font-weight: bold;
		margin: 0 auto 20px;
		border-radius: 50px;
		background-color: #1F1E48;
		&>span {
			display: inline-block;
			width: 50%;
			height: 100%;
			line-height: 80px;
			text-align: center;
		}
		&>.active {
			border-radius: 50px;
			background: linear-gradient(#814ABC, #4C44D8);
		}
	}
	.box {
		width: 100vw;
		height: 100vh;
		padding: 20px;
		overflow: hidden;
	}
</style>
