<template>
	<div class="box">
		<self-list
		  :title="$t('message.drawRecord')"
		  :subtitle="[$t('message.type'), $t('message.number'), $t('message.time')]"
		  :list="list"
		  span="8"
		></self-list>
	</div>
</template>

<script>
	import SelfList from '@/components/self-list.vue'
	
	export default {
		components: { SelfList },
		data() {
			return {
				list: []
			}
		},
		mounted() {
			this.$contract.redressWS.events.Event_AddNewRedress({
	filter:{ owner: this.$address },
	fromBlock:9130000
	}).on('data', res => {
				this.list.push({
					type: '',
					amount: `${this.$fromWei(res.returnValues.amount)} SFC`,
					time: '2020.01.15'
				})
			}).on('error', console.error)
			
			this.$contract.redressWS.events.Event_WithdrawRedress({
				filter:{ owner: this.$address },
	fromBlock:9130000
	}).on('data', res => {
				this.list.push({
					type: '',
					amount: `${this.$fromWei(res.returnValues.amount)} SFC`,
					time: '2020.01.15'
				})
			}).on('error', console.error)
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		padding: 40px 20px;
	}
</style>
